import React from 'react';
import Type from './javascripts/Type';
import '../styles/Home.scss';

const Home = () => {
    React.useEffect(() => {
        const type = Type();
        return () => {
            type.destroy();
        };
    }, []);
    return (
        <div className="container">
            <div className="home">
                <div className="content">
                    <h1>
                        <span className="name"></span>
                    </h1>
                    <h2>
                        <span className="introA"></span>
                        <span className="introB"></span>
                    </h2>
                    <p>
                        <span className="desc"></span>
                    </p>
                    <ul>
                        <li>
                            <a href="https://linkedin.com/in/virezee" target="_blank" rel="noreferrer">
                                <img className="l" src={require("../styles/imgs/linkedin.svg").default} alt="LinkedIn" />
                            </a>
                        </li>
                        <li>
                            <a href="https://github.com/VireZee" target="_blank" rel="noreferrer">
                                <img className="g" src={require("../styles/imgs/github.svg").default} alt="GitHub" />
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="home-img"></div>
        </div>
    )
}
export default Home;