import React from 'react';
import 'particles.js';
import PartConf from './javascripts/Particles';
import '../styles/Contact.scss';

const Contact = () => {
    React.useEffect(() => {
        const particlesJS = window.particlesJS;
        particlesJS('particles-js', PartConf);
    }, []);
    return (
        <div className="con-container">
            <div id="particles-js"></div>
            <div className="contact">
                <h1>Get in Touch</h1>
                <p>Feel free to contact me for any inquiries or collaboration opportunities.
                    Just click on the button below, leave a message, and I'll try my best to respond back to you!</p>
                <button><span><a href="mailto:viermarzo@gmail.com">Hello</a></span><i></i></button>
            </div>
        </div>
    )
}
export default Contact;