import $ from 'jquery';
import 'animate.css';
window.jQuery = $;
require('textillate');
require('letteringjs');

const Textillate = () => {
    $('h1').textillate({
        in: {
            effect: 'animate__animated animate__flash',
            delayScale: 1,
            delay: 7,
            shuffle: true
        }
    });
}
export default Textillate;