import $ from 'jquery';

const Navigation = (currentPath) => {
    $(".nav a").on("click", function () {
        let position = $(this).parent().position();
        let width = $(this).parent().width();
        $(".nav .slide1").css({ opacity: 1, left: + position.left, width: width });
    });
    $(".nav a").on("mouseover", function () {
        let position = $(this).parent().position();
        let width = $(this).parent().width();
        $(".nav .slide2").css({ opacity: 1, left: + position.left, width: width });
    });
    $(".nav a").on("mouseout", function () {
        $(".nav .slide2").css({ opacity: 0 });
    });
    let currentMenuItem = $(`.nav a[href="${currentPath}"]`).parent("li");
    if (currentMenuItem.length > 0) {
        let currentWidth = currentMenuItem.width();
        let current = currentMenuItem.position();
        $(".nav .slide1").css({ left: current?.left || 0, width: currentWidth });
    }
}
export default Navigation;