import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import Nav from './javascripts/Navigation';
import Ham from './javascripts/Hamburger';
import '../styles/Navbar.scss';

const Navbar = () => {
    const location = useLocation();
    const [isMobile, setIsMobile] = React.useState(false);
    const handleResize = () => {
        setIsMobile(window.innerWidth <= 800);
    };
    React.useEffect(() => {
        Nav(location.pathname);
        handleResize();
        window.addEventListener('resize', handleResize);
        Ham();
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, [location]);
    return (
        <nav className="nav-container">
            {!isMobile && (
                <ul className="nav">
                    <li><Link to="home">Home</Link></li>
                    <li><Link to="abt">About</Link></li>
                    <li><Link to="exp">Experiences</Link></li>
                    <li><Link to="pro">Projects</Link></li>
                    <li><Link to="con">Contact</Link></li>
                    <li className="slide1"></li>
                    <li className="slide2"></li>
                </ul>
            )}
            {isMobile && (
                <ul className="nav">
                    <div id="nav-icon">
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                </ul>
            )}
        </nav>
    );
};
export default Navbar;